/* header */
/* #head_wrap in layout.css */
/* color set >> light_mode.css / dark_mode.css */

/* header */

.head_inner {
  position: absolute;
  top: 0;
  width: 94vw;
  right: 3vw;
  border-radius: 15px;
  display: flex;
  height: 100%;
  overflow: hidden;
  span {
    &.symbol {
      display: flex;
      overflow-x: hidden;
      text-indent: -9999px;
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: 50%;
    }
    &.link_title {
      display: flex;
      overflow: hidden;
      text-indent: .5rem;
    }
  }
  > button[type="button"].menu{
    >span.symbol {
      display: none;
    }
  }
}
div.translate{
  button[type="button"] {
    display: flex;
    justify-self: center;
    justify-content: center;
    line-height: 1;
    padding: 0;
    &.option-btn {
      align-self: center;
      background-color: transparent;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      border-radius: 50%;
      text-indent: -9999px;
      &.ko {
        background-image: url("../../assets/images/korean.svg");
      }
      &.en {
        background-image: url("../../assets/images/usa.svg");
      }
      &.ja {
        background-image: url("../../assets/images/japan.svg");
      }
    }
  }
}

.small {
  width: 35vw;
  border-radius: 15px;
}


// resize 650px >> title text >> icon change
