@media screen and (min-width: 481px) {
  .head_inner {
    justify-content: space-between;
    transition: width 0.3s;
    > .gnb {
      display: -moz-grid;
      display: grid;
      width: 100%;
      height: 100%;
      grid-template-columns:  repeat(5, 1fr);
      justify-content: space-evenly;
      align-items: center;
      > a {
        display: inline-flex;
        width: 100%;
        height: 100%;
        text-indent: 5px;
        justify-content: center;
        align-items: center;
        background-color: none;
        -moz-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        -webkit-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        -o-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        > span.symbol {
          border-radius: 50%;
          margin-right: .5rem;
        }
        &.bold,
        &:hover {
          font-weight: bold;
          transform-origin: center;
        }
      }
    }
  }
  div.translate {
    display: flex;
    position: fixed;
    right: 2.5vw;
    border-radius: 30px;
    justify-content: space-between;
    overflow: hidden;
    transition: height 0.3s;
    > ul.lang{
      display: none;
    }
    > button[type="button"] {
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      display: flex;
      border-radius: 50%;
      background-image: url('/assets/images/a5705a70-f0c6-4a77-b9c4-d9f66892ac52.svg');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }
    &.active {
      transition: height 0.3s;
      > ul.lang {
        display:-moz-grid;
        display: grid;
        width: 100%;
        height: 100%;
        grid-template-rows:  repeat(3, 1fr);
        overflow: hidden;
        > li {
          display: flex;
          justify-content: center;
          width: 100%;
          height: 100%;
          border-radius: 15px;
          > button[type="button"] {
            &.option-btn {
              display: flex;
              justify-self: center;
              background-color: transparent;
              background-size: contain;
              background-position: center;
            }
            &.active,
            &:hover {
              transform-origin: center;
            }
          }
        }
      }
    }
  }

  .small {
    border-radius: 15px;
    margin-top: 5px;
    > .gnb {
      width: 100%;
      overflow: hidden;
      > a {
        background-color: transparent;
        width: 100%;
        height: 100%;
        align-items: center;
        -moz-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        -webkit-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        -o-transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
        >span {
          &.symbol {
            margin: 0 auto;
          }
          &.link_title {
            text-indent: -9999px;
            overflow: hidden;
          }
        }
        &.bold,
        &:hover {
          font-weight: bold;
          transform-origin: center;
        }
      }
    }
  }
}




